import React from 'react'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';

const ContactForm = ({ onSubmit, newName, nameChangeHandler, newNumber, numberChangeHandler }) => {
    return (
        <>
            <h3>Add new contact</h3>
            <Form onSubmit={onSubmit}>
                <Form.Group controlId="name">
                    <Form.Control
                        type="text"
                        value={newName}
                        onChange={nameChangeHandler}
                        placeholder="Name" />
                </Form.Group>
                <Form.Group controlId="number">
                    <Form.Control
                        type="text"
                        value={newNumber}
                        onChange={numberChangeHandler}
                        placeholder="Number" />
                </Form.Group>
                <Button className="mb-3" type="submit" variant="primary" block>Add</Button>
            </Form>
        </>
    )
}

export default ContactForm